import React, { useEffect, useRef } from 'react';
import {detectAllFaces} from 'face-api.js';
import Webcam from "react-webcam";
import * as faceapi from 'face-api.js';
import imgPath from './images/overlay-cap.png';
import './App.css';

const App = () => {
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);
  const hatImageRef = useRef(null);


  useEffect(() => {
    function parseQuery(queryString) {
      let query = {};
      let pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
      for (let i = 0; i < pairs.length; i++) {
          let pair = pairs[i].split('=');
          query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
      }
      return query;
    }

    let app = window.Telegram.WebApp;
    let query = app.initData;
    let user_data_str = parseQuery(query).user;
    // let user_data = JSON.parse(user_data_str)
    app.expand();
    app.ready();
    console.log(user_data_str);

    navigator.mediaDevices
    .getUserMedia({ video: { facingMode: 'user' } })
    .then((stream) => {
      webcamRef.current.srcObject = stream;
    })
    .catch((error) => {
      console.error('Error accessing camera:', error);
    });

    const startFaceDetection = async () => {
      const MODEL_URL = process.env.PUBLIC_URL + '/models';
      await faceapi.loadTinyFaceDetectorModel(MODEL_URL);
      await faceapi.loadFaceLandmarkModel(MODEL_URL);
      await faceapi.loadFaceRecognitionModel(MODEL_URL);
      await faceapi.loadSsdMobilenetv1Model(MODEL_URL);
      
      const canvas = canvasRef.current;

      const context = canvas.getContext('2d');

      setInterval(async () => {
        const detections = await detectAllFaces(webcamRef.current).withFaceLandmarks();
        context.clearRect(0, 0, canvas.width, canvas.height);

        detections.forEach((detection) => {
          const hatImage = hatImageRef.current;

          const landmarks = detection.landmarks;
          const leftEyeBrow = landmarks.getLeftEyeBrow();
          const rightEyeBrow = landmarks.getRightEyeBrow();

          const leftPoint = leftEyeBrow[0];
          const rightPoint = rightEyeBrow.splice(-1)[0];
          const width = (rightPoint.x - leftPoint.x)*2;

          canvas.width = width;
          canvas.height = 91;
          canvas.style.width = width + 'px';
          canvas.style.left =  (leftPoint.x - width * 0.10) - 10 + 'px';
          canvas.style.top = (leftEyeBrow[0].y - width * 0.55) + 'px';

          context.drawImage(hatImage, 0, 0, canvas.width, 91);
        });
      }, 300);
    };
    startFaceDetection();
  }, []);

  return (
    <div className='App'>
      {/* <Webcam
        className='App__video'
        ref={webcamRef}
        mirrored={false}
        style={{
          position: 'absolute',
          left: 0,
          top: 0,
          zIndex: 1,
        }}
      /> */}
      <video
      ref={webcamRef}
      autoPlay
      playsInline
      muted
      controls
      className='App__video'
      style={{
        position: 'absolute',
        left: 0,
        top: 0,
        zIndex: 1,
      }}
      />
      <canvas
        ref={canvasRef}
        style={{
          position: 'absolute',
          zIndex: 100,
          aspectRatio: '171 / 91'
        }}
        />
      <img
        ref={hatImageRef}
        src={imgPath} //    
        className='cap'
        alt="Hat"
        style={{
          display: 'none',
          zIndex: -1000,
          position: 'absolute',
        }}
      />
    </div>
  );
};

export default App;